import * as auth from "auth-provider";
import { FullPageErrorFallback, FullPageLoading } from "components/lib";
import React, { ReactNode } from "react";
import { useQueryClient } from "react-query";
import { User } from "types/user";
import { useMount } from "utils";
import { http } from "utils/http";
import { useAsync } from "utils/use-async";
interface AuthForm {
  username: string;
  password: string;
}
//初始化user,持久化页面，防止页面刷新token失效
const bootstrapUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await http("me", { token });
    user = data.user;
  }
  return user;
};
//hook context  用于保存用户登录信息等
const AuthContext = React.createContext<
  | {
      user: User | null;
      register: (form: AuthForm) => Promise<void>;
      login: (form: AuthForm) => Promise<void>;
      logout: () => Promise<void>;
    }
  | undefined
>(undefined);
AuthContext.displayName = "AuthContext";
//组件
export const AuthProvider = ({ children }: { children: ReactNode }) => {
  // const [user, setUser] = useState<User | null>(null);
  const {
    data: user,
    error,
    isLoading,
    isIdle,
    isError,
    run,
    setData: setUser,
  } = useAsync<User | null>();
  const queryClient = useQueryClient();
  //与下面等价 const register = (form: AuthForm) => auth.login(form).then(user => setUser(user))
  const login = (form: AuthForm) => auth.login(form).then(setUser);
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  const logout = () =>
    auth.logout().then(() => {
      setUser(null);
      queryClient.clear();
    });

  useMount(() => {
    run(bootstrapUser());
  });
  //初始或者加载为true，显示页面加载组件
  if (isIdle || isLoading) {
    return <FullPageLoading />;
  }
  //如果请求失败或其他错误则显示错误页面组件信息，不这样做会直接跳转到登录页面，可以防止用户重复登录都失败
  if (isError) {
    return <FullPageErrorFallback error={error} />;
  }
  //返回用户登录注册，退出，用户信息
  return (
    <AuthContext.Provider
      children={children}
      value={{ user, login, register, logout }}
    />
  );
};
//要使用用户信息可以使用这个hook拿到存放在context里的数据
export const useAuth = () => {
  //获取数据
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth必须在AuthProvider中使用");
  }
  return context;
};
