/**
 * 该文件定义错误边界组件
 */
import React from "react";
// https://github.com/bvaughn/react-error-boundary 官方错误边界插件
type FallbackRender = (props: { error: Error | null }) => React.ReactElement;
//这里要写class组件，因为错误边界的实现是一定要用class component来实现的
//React.Component<{children:ReactNode,fallbackRender:FallbackRender}>可以这样写，也可以像下面那样写，
export class ErrorBoundary extends React.Component<
  React.PropsWithChildren<{ fallbackRender: FallbackRender }>,
  { error: Error | null }
> {
  //TODO 当子组件抛出异常，state就不会是null了
  state = { error: null };
  /**
   * 定义错误边界方法
   * TODO 当ErrorBoundary组件的子组件发生了渲染错误，这个方法就会被调用，返回的值就会被赋值给state
   * @param error
   * @returns
   */
  static getDerivedStateFromError(error: Error) {
    return { error };
  }

  render() {
    const { error } = this.state;
    const { fallbackRender, children } = this.props;
    if (error) {
      return fallbackRender({ error });
    }
    return children;
  }
}
