/**
 * 该文件统一处理Lading和Error状态
 */
import { useState, useCallback } from "react";
import { useMountedRef } from "utils";
//定义类型
interface State<D> {
  error: Error | null;
  data: D | null;
  stat: "idle" | "loading" | "error" | "success";
}
//默认值
const defaltInitialState: State<null> = {
  stat: "idle",
  data: null,
  error: null,
};

//默认配置
const defaultConfig = {
  throwOnError: false,
};

export const useAsync = <D>(
  initialState?: State<D>,
  initialConfig?: typeof defaultConfig
) => {
  const config = { ...defaultConfig, ...initialConfig };
  const [state, setState] = useState<State<D>>({
    ...defaltInitialState,
    ...initialState,
  });
  const mountedRef = useMountedRef();
  //useState直接传入函数的含义是：惰性初始化，所以要用useState保存函数，不能直接传入函数
  const [retry, setRetry] = useState(() => () => {});
  const setData = useCallback(
    (data: D) =>
      setState({
        data,
        stat: "success",
        error: null,
      }),
    []
  );
  const setError = useCallback(
    (error: Error) =>
      setState({
        error,
        stat: "error",
        data: null,
      }),
    []
  );
  //run用来触发异步请求
  const run = useCallback(
    (promise: Promise<D>, runConfig?: { retry: () => Promise<D> }) => {
      //如果传入的不是promise 或者什么都不传入的话就报错
      if (!promise || !promise.then) {
        throw new Error("请传入Promise类型数据");
      }
      //重新调用run
      setRetry(() => () => {
        if (runConfig?.retry) {
          run(runConfig?.retry(), runConfig);
        }
      });
      //这里会出现无限循环，因为在usecallback或在依赖里用到了state又把state加到依赖里
      // setState({ ...state, stat: "loading" });
      //解决方法，我们可以不在回调函数里直接用state,使用state的另一种用法，函数用法
      setState((prevState) => ({ ...prevState, stat: "loading" }));

      return promise
        .then((data) => {
          if (mountedRef.current) setData(data);
          return data;
        })
        .catch((error) => {
          //catch会消化异常，如果不主动抛出,外面是接收不到异常的
          setError(error);
          //true手动抛出异常
          if (config.throwOnError) return Promise.reject(error);
          return error;
        });
    },
    [config.throwOnError, mountedRef, setData, setError]
  );

  return {
    isIdle: state.stat === "idle",
    isLoading: state.stat === "loading", //true || false
    isError: state.stat === "error",
    isSucess: state.stat === "success",
    run,
    setData,
    setError,
    //retry被调用时重新跑一遍run,让state刷新一遍
    retry,
    ...state,
  };
};
